@import url(http://fonts.googleapis.com/css?family=Open+Sans:300italic,400italic,700italic,300,400,700);
body{margin:0;font-family:"Open Sans", "Roboto", "Helvetica", "Arial", sans-serif;font-size:1rem;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:rgba(0,0,0,0.65)}a:link{text-decoration:none}.pages{position:relative;width:100%;display:flex;flex-direction:column;align-items:center}.section-title{margin-top:1.5rem;margin-bottom:0.5rem;font-size:1.5em}@media (max-width: 640px){.section-title{margin-top:0.5rem}}.pill{margin:0.25rem}.page{display:flex;flex-direction:column;justify-content:center;align-items:center;max-width:1000px}@media (max-width: 640px){.page{padding-left:20px;padding-right:20px}}ul{-webkit-padding-start:0px;padding-inline-start:0px}@media (max-width: 640px){.item-separator{display:none}}

.App{display:flex;flex-direction:column;justify-content:center;align-items:stretch}

.Nav{text-align:center}.ant-menu-item{padding:0px 10px}

.UserHeader{display:flex;justify-content:center;align-items:center;flex-direction:row;padding:20px}.UserHeader .title{font-size:3em}@media (max-width: 640px){.UserHeader .title{font-size:1.5em}}@media (max-width: 640px){.UserHeader{justify-content:center;align-items:center;padding:10px}}.UserHeader img{height:200px;width:200px;margin-right:2em;border-radius:50%;animation-delay:3s}@media (max-width: 640px){.UserHeader img{height:100px;width:100px;margin-right:1em;margin-bottom:1em}}.UserHeader .details{display:flex;flex-direction:column;align-items:flex-start}@media (max-width: 640px){.UserHeader .details{font-size:.8em}}.UserHeader .view-resume-link{display:flex;text-decoration:none;padding:0.75rem 1.5rem;font-weight:bold;align-items:center;margin-top:1rem;background-color:rgba(46,204,64,0.3);transition:background-color 250ms ease}.UserHeader .view-resume-link:hover{background-color:#2ecc40}.UserHeader .view-resume-link svg{fill:white;margin-left:8px}.UserHeader-avatar{transition:all .2s ease-in-out}.UserHeader-avatar:hover{transform:scale(1.1)}@keyframes avatar-grow{from{transform:scale(1)}to{transform:scale(1.1)}}

